import React from 'react'
import styles from './Calculator.module.css'

import TextField from '@material-ui/core/TextField'
import MenuItem from '@material-ui/core/MenuItem'
import FormControl from '@material-ui/core/FormControl'
import Select from '@material-ui/core/Select'
import InputLabel from '@material-ui/core/InputLabel'
import Divider from '@material-ui/core/Divider'
import Button from '@material-ui/core/Button'

import { useTranslate } from '../../hooks/useTranslate'
import { Product } from '../ProductComponent'

import queryString from 'query-string'
import Location from '../../../mobx/Location'

import payment_methods from './paymentMethods'

interface Calc {
	retval: number
	retdesc: string
	data: {
		price: number
		count: number
		amount: number
		currency: string
		commission: number
		free_pay: unknown
		sale_info: {
			common_base_price: number
			sale_percent: number
		}
	}
}

export default function Unit({ product }: Product) {
	const translate = useTranslate({
		buy: { en: 'Buy', ru: '' },
		way: { en: 'Payment method', ru: ' ' },
		minPrice: { en: 'Minimum you can buy', ru: '   ' },
		maxPrice: { en: 'Maximum you can buy', ru: '   ' },
	})

	const [paymentMethods, setPaymentMethods] = React.useState(0)
	const [currency, setCurrency] = React.useState(0)
	const [money, setMoney] = React.useState(0)
	const [gold, setGold] = React.useState(0)
	const [options, setOptions] = React.useState({})
	const [buy, setBuy] = React.useState(true)

	// console.log(paymentMethods)

	React.useEffect(() => {
		const uri = `https://api.digiseller.ru/api/products/price/calc?product_id=${product.id}&currency=${payment_methods[paymentMethods].currency}&unit_cnt=${product.prices_unit.unit_cnt_min}`

		fetch(uri)
			.then(response => response.json())
			.then((response: Calc) => {
				setGold(response.data.count)
				setMoney(response.data.amount)
			})

		product.options.forEach(option => {
			setOptions(prev => {
				return { ...prev, [option.name]: '' }
			})
		})
	}, [])

	const handleChangeOptions = (event: React.ChangeEvent<{ value: string; name: string }>) => {
		// setOptions(event.target.value as number)
		setOptions(prev => {
			return { ...prev, [event.target.name]: event.target.value }
		})
	}

	const handleChangePaymentMethods = (event: React.ChangeEvent<{ value: unknown }>) => {
		setCurrency(0)
		const uri = `https://api.digiseller.ru/api/products/price/calc?product_id=${product.id}&currency=${
			payment_methods[event.target.value as number].currency
		}&unit_cnt=${gold}`

		setBuy(false)
		fetch(uri)
			.then(response => response.json())
			.then((response: Calc) => {
				setMoney(response.data.amount)
				setGold(response.data.count)
				setBuy(true)
			})
		setPaymentMethods(event.target.value as number)
	}

	const handleChangeCurrency = (event: React.ChangeEvent<{ value: unknown }>) => {
		const uri = `https://api.digiseller.ru/api/products/price/calc?product_id=${product.id}&currency=${payment_methods[paymentMethods].currency}&unit_cnt=${gold}`

		setBuy(false)
		fetch(uri)
			.then(response => response.json())
			.then((response: Calc) => {
				setMoney(response.data.amount)
				setGold(response.data.count)
				setBuy(true)
			})
		setCurrency(event.target.value as number)
	}

	const handleChangeMoney = (event: React.ChangeEvent<{ value: unknown }>) => {
		const uri = `https://api.digiseller.ru/api/products/price/calc?product_id=${product.id}&currency=${payment_methods[paymentMethods].currency}&amount=${event.target.value}`

		setBuy(false)
		fetch(uri)
			.then(response => response.json())
			.then((response: Calc) => {
				setGold(response.data.count)
				setBuy(true)
			})
		setMoney(event.target.value as number)
	}

	const handleChangeGold = (event: React.ChangeEvent<{ value: unknown }>) => {
		const uri = `https://api.digiseller.ru/api/products/price/calc?product_id=${product.id}&currency=${payment_methods[paymentMethods].currency}&unit_cnt=${event.target.value}`

		setBuy(false)
		fetch(uri)
			.then(response => response.json())
			.then((response: Calc) => {
				setMoney(response.data.amount)
				setBuy(true)
			})
		setGold(event.target.value as number)
	}

	return (
		<div>
			<TextField
				id="standard-number"
				label={
					payment_methods[paymentMethods].currency === 'WMR'
						? ''
						: payment_methods[paymentMethods].currency === 'WMZ'
						? '$'
						: payment_methods[paymentMethods].currency === 'WME' && ''
				}
				type="number"
				InputLabelProps={{
					shrink: true,
				}}
				value={money}
				onChange={handleChangeMoney}
				className={styles.textboxNumber}
				style={{ marginRight: '2%' }}
			/>
			<TextField
				id="standard-number"
				label={product.prices_unit.unit_name}
				type="number"
				InputLabelProps={{
					shrink: true,
				}}
				value={gold}
				onChange={handleChangeGold}
				className={styles.textboxNumber}
				helperText={
					gold < product.prices_unit.unit_cnt_min
						? `${translate.minPrice} ${product.prices_unit.unit_cnt_min} ${product.prices_unit.unit_name}`
						: ''
				}
			/>
			<br />
			<br />
			<div style={{ display: 'flex', flexDirection: 'row' }}>
				<FormControl
					style={{
						marginRight: product.payment_methods[paymentMethods].currencies.length > 1 ? '2%' : 0,
						width: '100%',
					}}
				>
					<InputLabel htmlFor="payment_methods">{translate.way}</InputLabel>
					<Select
						native
						value={paymentMethods}
						onChange={handleChangePaymentMethods}
						inputProps={{
							id: 'payment_methods',
						}}
					>
						{payment_methods.map((method, index) => {
							return (
								<option key={method.name[Location.getLanguage()]} value={index}>
									{method.name[Location.getLanguage()]}
								</option>
							)
						})}
					</Select>
				</FormControl>
				{/* {product.payment_methods[paymentMethods].currencies.length > 1 ? (
					<FormControl style={{ width: 80 }}>
						<InputLabel htmlFor="currency"></InputLabel>
						<Select
							native
							value={currency}
							onChange={handleChangeCurrency}
							inputProps={{
								id: 'currency',
							}}
						>
							{product.payment_methods[paymentMethods].currencies.map((currency, index) => {
								return (
									<option key={currency.code} value={index}>
										{currency.currency}
									</option>
								)
							})}
						</Select>
					</FormControl>
				) : null} */}
			</div>
			<br />
			<Divider />
			<br />
			{product.options.length ? (
				<>
					{product.options.map((option, index) => {
						if (option.type === 'text') {
							return (
								<>
									<TextField
										key={option.name}
										name={option.name}
										label={option.label}
										onChange={handleChangeOptions}
										type="text"
										InputLabelProps={{
											shrink: true,
										}}
										// value={money}
										// onChange={handleChangeMoney}
										style={{ width: '100%' }}
									/>
									{index !== product.options.length - 1 ? (
										<>
											<br />
											<br />
										</>
									) : null}
								</>
							)
						}
					})}
				</>
			) : null}
			<br />
			<br />
			{/* <Button
				onClick={() => {
					console.log(options)

					fetch('https://api.digiseller.ru/api/purchases/options', {
						method: 'POST',
						headers: {
							Accept: 'application/json',
							'Content-Type': 'application/json',
						},
						body: JSON.stringify({
							product_id: product.id,
							options: Object.entries(options).map(([key, value]) => {
								return { id: key, value: { text: value } }
							}),
							unit_cnt: gold,
							lang: Location.getLanguage(),
							// ip: Location.getIP(),
						}),
					})
						.then(response => response.json())
						.then(response => {
							window.open(
								`https://www.digiseller.market/asp2/pay_wm.asp?id_d=${product.id}&id_po=${response.id_po}&failpage=${
									window.location
								}&lang=${Location.getLanguage()}&curr=${product.payment_methods[paymentMethods].currencies[currency].code}`,
								'_self'
							)
						})
				}}
				disableElevation
				variant="contained"
				color="primary"
				className={styles.buyBtn}
				disabled={Object.values(options).some(e => !(e as string).trim())}
			>
				{translate.buy}
			</Button> */}
			{Object.values(options).some(e => !(e as string).trim()) || !buy ? (
				<>
					<Button disabled disableElevation variant="contained" color="primary" className={styles.buyBtn}>
						{translate.buy}
					</Button>
				</>
			) : (
				<>
					{/* WEBMONEY */}
					{(payment_methods[paymentMethods].id === 1 ||
						payment_methods[paymentMethods].id === 2 ||
						payment_methods[paymentMethods].id === 3 ||
						payment_methods[paymentMethods].id === 4) && (
						<form method="POST" acceptCharset="windows-1251" action="https://merchant.webmoney.ru/lmi/payment.asp">
							<input type="hidden" name="LMI_PAYMENT_AMOUNT" value={money} />
							<input
								type="hidden"
								name="LMI_PAYEE_PURSE"
								value={
									payment_methods[paymentMethods].id === 1
										? 'R730367370692'
										: payment_methods[paymentMethods].id === 2
										? 'P568626562560'
										: payment_methods[paymentMethods].id === 3
										? 'Z293951360490'
										: payment_methods[paymentMethods].id === 4
										? 'E198152075649'
										: ''
								}
							/>
							<input
								type="hidden"
								name="LMI_PAYMENT_DESC"
								value={`: ${product.name}; : ${gold}; ${
									Object.entries(options).length &&
									Object.entries(options)
										.map(([key, value], index) => {
											return `${product.options[index].label}: ${value};`
										})
										.join(' ')
								}`}
							/>
							<input type="hidden" name="LMI_MODE" value="1" />

							<Button type="submit" disableElevation variant="contained" color="primary" className={styles.buyBtn}>
								{translate.buy}
							</Button>
						</form>
					)}

					{/* YOOMONEY */}
					{(payment_methods[paymentMethods].id === 5 ||
						payment_methods[paymentMethods].id === 6 ||
						payment_methods[paymentMethods].id === 7) && (
						<form method="POST" action="https://yoomoney.ru/quickpay/confirm.xml">
							<input type="hidden" name="receiver" value="41001978306913" />
							<input type="hidden" name="formcomment" value={product.name} />
							<input type="hidden" name="short-dest" value={product.name} />
							<input type="hidden" name="label" value={product.name} /> <input type="hidden" name="quickpay-form" value="donate" />
							<input type="hidden" name="targets" value={product.name} />
							<input type="hidden" name="sum" value={money} data-type="number" />
							<input
								type="hidden"
								name="comment"
								value={`: ${product.name}; ${
									Object.entries(options).length &&
									Object.entries(options)
										.map(([key, value], index) => {
											return `${product.options[index].label}: ${value};`
										})
										.join(' ')
								}`}
							/>
							<input type="hidden" name="need-fio" value="false" /> <input type="hidden" name="need-email" value="true" />
							<input type="hidden" name="need-phone" value="false" /> <input type="hidden" name="need-address" value="false" />
							<input
								type="hidden"
								name="paymentType"
								value={
									payment_methods[paymentMethods].id === 5
										? 'PC'
										: payment_methods[paymentMethods].id === 6
										? 'AC'
										: payment_methods[paymentMethods].id === 7
										? 'MC'
										: ''
								}
							/>
							<Button type="submit" disableElevation variant="contained" color="primary" className={styles.buyBtn}>
								{translate.buy}
							</Button>
						</form>
					)}

					{payment_methods[paymentMethods].id === 8 && (
						<Button
							href={`https://qiwi.com/payment/form/99
							?extra%5B%27account%27%5D=79267349056
							&amountInteger=${money.toString().split('.')[0]}
							&amountFraction=${money.toString().split('.')[1] ?? 0}
							&extra%5B%27comment%27%5D=${`: ${product.name}; : ${gold}; ${
								Object.entries(options).length &&
								Object.entries(options)
									.map(([key, value], index) => {
										return `${product.options[index].label}: ${value};`
									})
									.join(' ')
							}`}
							&currency=643
							&blocked[0]=account
							`}
							target="_blank"
							disableElevation
							variant="contained"
							color="primary"
							className={styles.buyBtn}
						>
							{translate.buy}
						</Button>
					)}
				</>
			)}
		</div>
	)
}
